body {
  font-family: 'Source Sans Pro', sans-serif;
}

.main {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  /* height: calc(100% - 50px); */
  padding: 0 12%;
}

@media (max-width: 380px) {
  .main {
    flex-direction: column;
    padding: 0 5%;
  }

}
